
.ContainerAforo{
    /*width: 100%;*/
    padding: 0 2% 3% 4%;
    font-size: 29px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 60vh;
}
.Contenedor_grafica_ilustracion{
    display: grid;
    display: flex;
    grid-template-columns: repeat(2,1fr); 
    align-items: center;
}
.contador{
    font-size: 18px;
    padding-top: 55px;
    padding-bottom: 35px;
    animation-name: llegadaDerecha;
    animation-duration: 4s;
}
@keyframes llegadaDerecha {
    0%{
        opacity: 0;
        transform:translateX(-2000px) ;
    }
    60%{
        opacity: 0.8;
        transform: translateX(15px)
    }
    80%{
        opacity: 0.9;
        transform: translateX(0px);
    }
    100%{
        opacity: 1;
        transform: translateX(0); /*Se devuelve*/
    }
}

.Grafica {
    height: 350px;
    width: 100%;
}
#aforo_img{
    align-items: center;
    justify-content: center;
    height: 260px;
    width: 100vh;   
}
