
.ContainerEdad{
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 18px;
    height: 69vh;
}
.contador{
    animation-delay: -1s
}
.Conjunto{
    display: flex;
    align-items: center;
   display: grid;
    grid-template-columns: repeat(2, 1fr);   
}

.Grafica1 {
    height: 400px;
    padding-left: 15%;
    padding-bottom: 12px;
    width: 100%;
}
.ContainerBoton1{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
    margin-top: 12px;
}
#edad_img{
    height: 250px;
    padding-left: 15%;
    display: flex;
}
@media screen and (min-width:375px) and (max-width:425px) {
    .Conjunto{
        grid-template-columns: repeat(1, 1fr)
    }
    .Grafica1{
        width: 90%;
        padding: 0; 
    }
    #edad_img{
        height: 200px;
        display: flex;
    }

    .ContainerEdad{
        padding-left: 15%;
    }
    .contador{
        width: 100%;
    }
}
@media screen and (min-width:426px) and (max-width:700px) {
    .Conjunto{
        display: grid;
        grid-template-rows: repeat(2, 1fr);
    }

}
